.alert{display:block;margin-bottom:8px;padding:5px 8px;border:1px solid #777}.alert:empty{display:none}.modal-alert{margin:-10px -10px 16px;padding:10px;border-top:none;border-left:none;border-right:none}.alert-danger{border-color:#ebccd1;background-color:#f2dede;color:#a94442}.alert-warning{border-color:#faebcc;background-color:#fcf8e3;color:#8a6d3b}.alert-info{border-color:#bce8f1;background-color:#d9edf7;color:#31708f}.alert-success{border-color:#d6e9c6;background-color:#dff0d8;color:#3c763d}.alert-big{font-size:14px;padding:10px 16px}.page-header .alert{clear:left;float:left}.page-notifs .alert{padding:8px 10px}.page-notifs .alert:last-child{margin-bottom:16px}